@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,500&display=swap");

:root {
  --main-color: #EB5757;
  --secondary-color: #f8f5f5;
  --white-color: #FFFFFF;
  --title-section: #333333;
  --subtext-color: #6B6B6B;
  --active-color: #505050;
  --inactive-color: #505050;
  --button-color: #333333;
  --radius: 20px;
  --about-color: #495867;
}
.App{
  max-width:  1280px;
  margin: 20px auto;
  border-radius: 20px;
  background-color:  var(--white-color);
  position: relative;
  overflow: hidden;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
}

html,
body,
#root {
  margin: 0;
  height: 100%;
  color: #000;
}
body {
  background-color: var(--secondary-color);
  -webkit-font-smoothing: antialiased;
}

body,
input,
button {
  font-family: "Open Sans", sans-serif;
}
 