@import url(Global.css);
 
/* About me */
header{
  height: 440px;
  display: flex;
  align-items: center;
}

.profileImages{
  width: 452px;
  height: 440px;
  position: relative;
  background-color: var(--main-color);
}
.profileImages img.avatar{
 position: absolute;
 bottom: 0;
 right: -100px;
}
.plus{
  position: absolute;
  top:10px;
  left: 10px;
}
.aboutMe {
  width: 460px;
  height: 253px;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  margin-left: 200px;
  margin-top: 80px;
}


.mainText{
  font-style: normal;
  font-weight: bold;
  font-size: 48px;
  line-height: 65px;
  color: var(--about-color)
}
.mainText span{
  font-style: normal;
font-weight: normal;

font-size: 36px;
line-height: 30px;
display: block;
}

.mainDescription{ 
  
width: 400px;
font-style: normal;
font-weight: normal;
font-size: 18px;
line-height: 135%;
color:rgba(73, 88, 103, 0.52); 
margin-top: 8px;
}

.frontEndb {
  font-style: normal;
  font-weight: normal;
  font-size: 66px;
  line-height: 85.69%;
  display: flex;
  align-items: center;
  text-align: center;
  color: rgba(73, 88, 103, 0.08);
  transform: rotate(-90deg);
  
  position: absolute;
  right: -75px;
}

a.btn{
  outline: none;
  text-decoration: none;
  &:hover, &:visited{
  outline: none;
  text-decoration: none;
  }
}

button, .btn {
  background: #333333;
  box-shadow: 0px 8px 14px rgba(0, 0, 0, 0.09);
  border-radius: 42px; 
  margin-top: 21px;
  outline: none;
  color: white;
  border: 0;
  height: 44px;
  padding: 10px 36px;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;  
  display: block;
  text-align: center;
}

.inlineFlex {
  display: flex;
}

.inlineFlex .btn:not(:first-child){
  margin-left: 20px;
}

.codepenBtn img {
  width: 24px;
}
.btnIcon{
  padding: 10px 36px 10px 26px;
  display: flex;
  align-items: center;
}
.btnIcon svg{
  margin-right: 16px;
}
.btn.codepenBtn {
  width: 44px;
  height: 44px;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
} 


/* Main */


main{
  margin-top:  80px;
  color: #000;
  padding: 0px 60px;
}

.experienceSection{ 
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.workExperience, .mySkills {
  display: flex;
}

.experienceSection h1  {
  width: 166px; 
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 41px;
  position: relative;
}
h1.headerSection{
  
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 41px;
  position: relative;
}

.experienceSection h1::after , h1.headerSection::after  {
  content: '';
  display: block;
  position: absolute;
  margin-top: 2px;
  width: 114px;
  height: 4px;
  background-color: #eb5757;
}
.experienceSection{
ul {
  list-style: none;
  margin-left: 26px;
  display: flex;
  flex-direction: column; 
}
ul li:not(:first-child){
  margin-top: 32px
}
ul li h1{
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 33px;
  /* identical to box height */
  margin-bottom: 4px;  
  color: #000000;
}

ul li p{
  font-style: normal;
font-weight: normal;
font-size: 14px;
line-height: 19px;
max-width: 280px;
/* identical to box height */ 

color: #6B6B6B;
}


}

.latestWork{
margin-top: 80px; 

h2{
  font-style: normal;
font-weight: 600;
font-size: 24px;
line-height: 33px;
}
}

.workCard{
  display: flex;
  flex-direction: row;
  margin-bottom: 80px;

  .btn.fitcontent{
    min-width: 180px;
  }

 picture{
  width: 356px;
  height: 222px; 
  border-radius: 8px;
  overflow: hidden;   
  align-self: center;  
  box-shadow: 2px 4px 15px rgba(0,0,0,0.15);

 }
}

.workCard img { 
  height: 222px;
}

.cardRow.spaceAround {
  display: flex;
  flex-direction: row;
  justify-content: space-around;

  picture{
    
    margin-right: 46px;
    }
    .workCard{
      .workCardInfo {
    
        align-items: flex-start; 
      }
  
    }
    .workCard.flexInverse{
      flex-direction: row-reverse;
      justify-content: space-around;
      picture{
        margin-right: 0px;
        margin-left: 46px;
        }

    }
}


.cardRow{
  display: flex;
  flex-direction: row;
  &.spaceBetween{ 
  justify-content: space-between;

  picture{
    
  margin-bottom: 24px;
  }

  .workCard{
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;

  

  }
  }
}

.workCardInfo {
  max-width: 417px; 
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.workDescription{margin-top: 24px;}
.smallText{
  width: 100%;
  font-style: normal;
font-weight: normal;
font-size: 14px;
line-height: 16px;

/* sub-text */

color:  var(--subtext-color)
}
.workCardInfo p:not(:first-child){

  margin-bottom: 12px;
}

.btn.fitcontent {
  
  width: fit-content;
}



footer{

  width: 100%;
  padding: 36px;
  background: #495867;
  text-align: center;
  color: white;
  margin-top: 60px;
}



@media (max-width: 1280px) { 
  
  .workCard .workCardInfo {
    max-width: 417px; 
    display: flex;
    flex-direction: column !important;
    align-items: center !important;
    justify-content: center !important;
  }
  
  .cardRow {
    display: flex;
    flex-direction: column;
}

.cardRow{
  display: flex;
  flex-direction: column !important;
  justify-content: space-between !important;

  picture{
    
  margin-left: 0px !important;
  margin-right: 0px !important;
  margin-bottom: 24px !important;
  }

  .workCard{
    flex-direction: column !important;
    text-align: center !important;
    align-items: center !important;
    justify-content: center !important;

   
  }
  }
 
  .experienceSection {
    display: grid;
    grid-template-columns: 1fr ;
}
 
}

@media (max-width:785px){
  .aboutMe {
    width: auto;
    margin-left: 60px;
    margin-top: 0px;
    z-index: 2;
  
  }
   
  
  .profileImages { 
    width: 40px;
    height: 440px;
    position: relative; 
    background-color: var(--main-color);
   
  }
  .profileImages img.avatar {
   display: none;
  }

  
}